body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.login__button {
  display: inline-block;
  background: white;
  color: #444;
  /* width: 150px; */
  border-radius: 4px;
  border: thin solid #888;
  box-shadow: 1px 1px 1px grey;
  white-space: nowrap;
}

.login__button:hover {
  cursor: pointer;
}

.login__button:active {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14),
    0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.login__button_icon {
  background: url("data:image/png;base64,")
    transparent 5px 50% no-repeat;
  display: inline-block;
  vertical-align: middle;
  width: 42px;
  height: 42px;
}

.login__button_text {
  display: inline-block;
  vertical-align: middle;
  padding-left: 22px;
  padding-right: 22px;
  font-size: 14px;
  font-weight: bold;
  /* Use the Roboto font that is loaded in the <head> */
  font-family: "Roboto", sans-serif;
}

.login__page {
  font-family: "Quicksand", sans-serif;
  /* color: rgb(173, 173, 194); */
  width: 100vw;
  height: 100vh;
  background: url(/react-google-tasks/image/natural.jpg) center no-repeat;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.login__form {
  padding-top: 8px;
  padding-bottom: 8px;
  min-width: 45vw;
  min-height: 30vh;
  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}


.task__item_text {
  overflow-wrap: break-word;
  margin-right: 55px;
}

.task__item_chip {
  margin: 8px;
  margin-left: 0;
}

.tasks__card {
  width: 100%;
  background-color: #fff;
}

.tasks__add-button {
  margin: 8px auto !important;
}

.tasks__add-button_icon {
  margin-right: 8px;
}

:root {
  --drawerWidth: 240px;
}

.tasks__page {
  display: -webkit-flex;
  display: flex;
}

.tasks__app-bar {
  width: calc(100% - 240px) !important;
  width: calc(100% - var(--drawerWidth)) !important;
  margin-left: 240px;
  margin-left: var(--drawerWidth);
}

.tasks__app-name {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.tasks__drawer {
  width: 240px;
  width: var(--drawerWidth);
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.tasks__drawer-paper {
  width: 240px;
  width: var(--drawerWidth);
}

.tasks__toolbar {
  min-height: 56px;
}

@media (min-width: 0px) and (orientation: landscape) {
  .tasks__toolbar {
    min-height: 48px;
  }
}

@media (min-width: 600px) {
  :root {
    --drawerWidth: 300px;
  }

  .tasks__toolbar {
    min-height: 64px;
  }
}

.tasks__content {
  width: calc(100% - 240px) !important;
  width: calc(100% - var(--drawerWidth)) !important;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background-color: #fafafa;
  padding: 24px;
}

.loader {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}


